import { Component, OnInit, Output, EventEmitter, Input, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { NotificationService } from 'src/app/services/notification.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Role } from 'src/app/models/role.model';


@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.scss']
})
export class UserFormComponent extends ResourceFormContent {
  title: string;
  message: string;
  formGroup: FormGroup;

  roles: Role[];

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    http.get<any[]>(environment.prepareUrl('rolesFetchUrl')).toPromise().then(res => this.roles = res);

    this.formGroup = new FormGroup({
      user_name: new FormControl(null, Validators.required),
      email: new FormControl(null, Validators.required),
      first_name: new FormControl(null, Validators.required),
      last_name: new FormControl(null, Validators.required),
      password: new FormControl(null, Validators.minLength(8)),
      password_confirmation: new FormControl(null, Validators.minLength(8)),
      role_id: new FormControl(null, Validators.required)
    });
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static'
    };

    return this.service.open(this.content, options);
  }

  postUrl(): string {
    return 'usersCreateUrl';
  }
  putUrl(): string {
    return 'usersCreateUrl';
  }
}
