import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';

import { User } from 'src/app/models/user.model';
import { ActivatedRoute, Router } from '@angular/router';
import { TableViewComponent, ITableContext } from 'src/app/shared/dashboard-resource/table-view/table-view.component';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: User[];
  context: ITableContext<User> = {
    urls: {
      createUrl: '/users/create',
      detailsUrl: '/:id'
    },
    data: this.data,
    properties: [
      'user_name',
      'email',
      'role_id',
      'status_id'
    ],
    title: 'Users'
  };
  @ViewChild('table') table: TableViewComponent<User>;

  constructor(route: ActivatedRoute) {
    route.data.subscribe(data => {
      this.data = data.users;
      this.context.data = this.data;
    });
  }
}
