import { NgModule, Type } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './index/index.component';

import { UsersResolver } from './users.resolver';
import { UserDetailsComponent } from './user-details/user-details.component';
import { UserFormComponent } from './user-form/user-form.component';
import { ResourceFormComponent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { UserDeleteComponent } from './user-delete/user-delete.component';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    resolve: {
      users: UsersResolver
    },
    children: [
      {
        path: 'create',
        component: UserFormComponent,
      },
      {
        path: ':id/edit',
        component: UserFormComponent,
      },
      {
        path: ':id/details',
        pathMatch: 'full',
        component: UserDetailsComponent,
      },
      {
        path: ':id/delete',
        pathMatch: 'full',
        component: UserDeleteComponent,
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UsersRoutingModule {
  constructor() {
    this.registerResourceComponents();
  }

  private registerResourceComponents(): void {
    ResourceFormComponent.TYPES.set('userForm', UserFormComponent);
  }
}
